import React from "react";
import img from '../images/logo.png';
import { NavLink } from "react-router-dom";
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import InstagramIcon from '@mui/icons-material/Instagram';
import EmailIcon from '@mui/icons-material/Email';

const Navbar = () => {
    return(
        <>
            <header>
            
                <div className="marquee">
                    <marquee behavior="" direction="left">ऑनलाइन फार्म आवेदन केन्द्र (साइवर कैफे) आपके घर से काफी दूर है या साइबर कैफे में भीड़ होने के कारण आपको घंटो इंतजार करना पड़ता</marquee>
                </div>
            
                <div className="navbar  p-3 px-5">
                    <div className="logo">
                        <img src={img} alt="logo" width={'200px'}/>
                    </div>

                    <div className="nav-links">
                    
                        <NavLink to="" className="active"><WhatsAppIcon/></NavLink>
                        <NavLink to="" className="active"><InstagramIcon/></NavLink>
                        <NavLink to="" className="active"><EmailIcon/></NavLink>
                        {/* <NavLink to="/contact" activeclassName="active">Contact</NavLink> */}
                    </div>
                </div>

            
            </header> 
        </> 
    );
};

export default Navbar;