import React,{useEffect} from "react";
import img from '../images/heading_border.png';
import AOS from 'aos';
import 'aos/dist/aos.css';

const Files = () => {
    useEffect(()=>{
        AOS.init({duration:1500})
    })
    return (
        <>
        <div id="files">  
        <div className="heading text-center pt-3" >
            <h2 style={{marginBottom:'0rem'}}>Upload Files</h2>
            <img src={img} alt="Below heading" />
        </div>
        <div className="files w-75 m-auto" data-aos="fade-right">
        <div className="mb-3">
        <label for="exampleFormControlInput1" className="form-label">Name</label>
        <input type="email" className="form-control" id="exampleFormControlInput1" placeholder="Username"/>
        </div>
        <div className="mb-3">
        <label for="exampleFormControlInput1" className="form-label">Email</label>
        <input type="email" className="form-control" id="exampleFormControlInput1" placeholder="Email address"/>
        </div>
        <div className="mb-3">
        <label for="exampleFormControlInput1" className="form-label">Phone</label>
        <input type="email" className="form-control" id="exampleFormControlInput1" placeholder="Phone Number"/>
        </div>
        <div className="mb-3">
        <label for="exampleFormControlInput1" className="form-label">Choose Form</label>
        <select class="form-select" aria-label="Default select example">
            <option selected>Open this select menu</option>
            <option value="1">One</option>
            <option value="2">Two</option>
            <option value="3">Three</option>
        </select>
        </div>
        <button className="upload-btn mt-2  btn btn-primary">Submit</button>
        </div>
        </div>
        </>
    )
};

export default Files;