import React,{useEffect}from "react";
import img from '../images/heading_border.png';
import { NavLink } from "react-router-dom";
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import InstagramIcon from '@mui/icons-material/Instagram';
import EmailIcon from '@mui/icons-material/Email';
import TwitterIcon from '@mui/icons-material/Twitter';
import AOS from 'aos';
import 'aos/dist/aos.css';

const Contact = () => {
    useEffect(()=>{
        AOS.init({duration:1500})
    })
    return (
        <>
        <div className="my-5 text-center heading" id="Services">
            <h2 style={{marginBottom:'0rem'}}>Contact Us</h2>
            
            <img src={img} alt="Below heading" />
            
        </div>

        <div className="contact-section shadow p-5" data-aos="zoom-in">
            <div className="contact-text">
                <div className="contact-heading">
                    <h3>Need Help?</h3>
                    <p>Our specialists will contact you for detail and clarification.</p>
                </div>

                <div className="contact-links">
                    <NavLink to="/" className="active"><WhatsAppIcon/></NavLink>
                    <NavLink to="/" className="active"><InstagramIcon/></NavLink>
                    <NavLink to="/" className="active"><TwitterIcon/></NavLink>
                    <NavLink to="/" className="active"><EmailIcon/></NavLink>
                </div>
                
            </div>
            
            <div className="contact-form">
                <div className="first">
                    <label for="exampleFormControlInput1" >Name</label><br />
                    <input type="text" className="w-100" id="exampleFormControlInput1" placeholder="Enter your name"/><br />
                </div>
                <div className="second d-flex mt-3">
                    <div className="phone">
                    <label for="exampleFormControlInput1" >Phone</label><br /> 
                    <input type="phone" id="exampleFormControlInput1" placeholder="Enter Phone Number"/>
                    </div>
                    <div>
                    <label for="exampleFormControlInput1" >Email</label><br />
                    <input type="email"  id="exampleFormControlInput1" placeholder="Enter Email address"/>
                    </div>
                </div>
                <button className="mt-4 btn btn-primary">Submit</button>
            </div>
        </div>
        </>
    );
};

export default Contact;