import React from "react";
import Data from "./Data";
import Card from "./Card";
import img from '../images/heading_border.png';

const Services = () => {
    return (
        <>
        <div className="my-5 text-center heading" id="Services">
            <h2 style={{marginBottom:'0rem'}}>Our Services</h2>
            
            <img src={img} alt="Below heading" />
        </div>
        <div className="container-fluid mb-5">
            <div className="row">
                <div className="col-10 mx-auto">
                    <div className="row gy-5">
                    {Data.map((val,index) =>{
                        return <Card
                            key={index}
                            id = {index}
                            image = {val.imgsrc}
                            name = {val.name}
                        />
                    })}
                    </div>
                </div>
            </div>
        </div>
         
        </>
    );
};


export default Services;